<template>
  <div class="myHome">
    <div style="padding: 20px">
      <!--面包屑-->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '' }">

          <span style="color: white">用户中心</span>
        </el-breadcrumb-item>
        <el-breadcrumb-item>

          <span style="color: white">日志中心</span>
        </el-breadcrumb-item>
      </el-breadcrumb>


      <div>
        <div style="padding: 10px ;float: left">
          <el-input style="width: 200px" placeholder="请输入操作者" suffix-icon="el-icon-search" class="ml-5"
                    v-model="operator"></el-input>
        </div>

        <div style="padding:10px;float: left">
          <el-col :span="80">
            <div class="sub-title"></div>
            <el-autocomplete
              class="inline-input"
              v-model="logKeyWord"
              :fetch-suggestions="querySearch"
              placeholder="输入关键词筛选日志"
              @select="handleSelect"
            ></el-autocomplete>
          </el-col>
        </div>

        <!--加value-format，后端用JSONFormat解决前后端传时间的时区问题-->
        <div class="block" style="padding:10px;float: left">
          <el-date-picker
            v-model="timeValue"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            align="right"
            @change="getTimeFilter"
            value-format="yyyy-MM-dd HH:mm:ss"
          >
          </el-date-picker>
        </div>
        <div style="padding: 10px;float: left">
          <el-button type="primary" @click="load">筛选</el-button>
        </div>
        <div style="padding: 10px ;float: left;">
          <el-button style="margin-left: 10px" type="success" @click="clearFilter">重置筛选条件</el-button>
        </div>
        <!--<div style="padding: 10px ;float: left">-->
        <!--  <el-button style="margin-left: 10px" type="danger" @click="deleteDialog">删除日志记录</el-button>-->
        <!--</div>-->


      </div>

      <!--    表格部分-->
      <div>
        <div>
          <el-table :data="tableData"
                    border stripe
                    :header-cell-style="{background:'#488DD9',color:'white'}"
                    :stripe=true
                    :highlight-current-row="true"
                    style="font-size: 15px"
                    max-height="600"
                    @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" min-width="20%">
            </el-table-column>
            <el-table-column prop="operator" label="操作者" min-width="20%">
            </el-table-column>
            <el-table-column prop="action" label="操作类型" min-width="20%">
            </el-table-column>
            <el-table-column prop="logInfoTime" label="操作时间" min-width="20%">
            </el-table-column>
            <!--          <el-table-column prop="missionStatus" label="任务状态" min-width="20%">-->
            <!--          </el-table-column>-->
            <el-table-column prop="object" label="操作信息" min-width="20%">
            </el-table-column>
          </el-table>
        </div>
        <div style="padding: 10px 0;background-color: white">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[2, 5, 10,20]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>

      <!--  删除提示框-->
      <el-dialog title="提示" :visible.sync="delVisible" width="300px" center append-to-body>

        <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>

        <span slot="footer" class="dialog-footer">

          <el-button @click="delVisible = false">取 消</el-button>

          <el-button type="primary" @click="deleteBatch">确 定</el-button>
      </span>

      </el-dialog>






    </div>
  </div>

</template>

<script>
import '../css/myCss.css'
export default {
  name: "SystemLog",
  data() {
    return {

      value: '',


      //  删除提示框的可见状态
      delVisible: false,

      //批量选择的数据
      multipleSelection: [],


      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]

      },

      //绑定时间选择器
      //第一个是选择器组件的选取的值，是一个数组，第2、3是实际要传给后端的，分开传，一个起始，一个结束
      //时间选择器组件用一个@Change监听，当改变的时候分别把timeValue数组里的第0、1个元素赋值给timeStart、timeEnd
      timeValue: "",
      timeStart: "",
      timeEnd: "",

      //输入带提示的框,这个数组加注提示词
      logTypes: [],


      //筛选器里的操作者IP和
      operator: "",
      logKeyWord: "",


      //表格部分数据
      tableData: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
    };
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      console.log(this.operator);
      console.log(this.logKeyWord);
      console.log(this.timeValue)
      this.request.get("/logInfo/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          operator: this.operator,
          logKeyWord: this.logKeyWord,
          timeStart: this.timeStart,
          timeEnd: this.timeEnd,
        }
      }).then(res => {
        this.tableData = res.records;
        this.total = res.total;
      })

    },
    querySearch(queryString, cb) {
      var logTypes = this.logTypes;
      var results = queryString ? logTypes.filter(this.createFilter(queryString)) : logTypes;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (logType) => {
        return (logType.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    loadAll() {
      return [
        {"value": "上传新资源"},
        {"value": "资源包审核拒绝"},
        {"value": "资源包上架"},
        {"value": "资源包下架"},
        {"value": "资源包删除"},
        {"value": "资源包扫描"},
        {"value": "未定义行为"},
        {"value": "异常错误"},
        // { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
      ];
    },
    handleSelect(item) {
      console.log(item);
    },


    //表格翻页方法
    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load() //刷新数据
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load() //刷新数据
    },

    //绑定时间筛选器的值
    getTimeFilter() {
      if (this.timeValue != null){
        this.timeStart = this.timeValue[0];
        this.timeEnd = this.timeValue[1];
      }
      console.log(this.timeStart);
      console.log(this.timeEnd);
    },



    //清除筛选方法
    clearFilter() {
      this.operator = "";
      this.logKeyWord = "";
      //一个时间筛选器清空,搜Google好像清空时间筛选器没有这么简单，但是我好像直接给这个V-model清零，暂时没看到什么问题
      this.timeValue = "";
      this.timeStart = "";
      this.timeEnd = "";
      //清空条件以后重新load一下
      this.load();
    },


    //操作多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(val)
    },

    //唤出确定删除对话框，然后确定按钮再去调用deleteBatch方法
    deleteDialog() {
      let ids = this.multipleSelection.map(v => v.logId)
      console.log(ids.length);
      //检测有没有选任务, 有再打开对话框，没有不打开
      if (ids.length == 0) {
        this.$message.warning("提示：没有选取任何一条日志记录")
      } else
        this.delVisible = true;
    },

    //批量删除
    deleteBatch() {
      //检测再对话框打开之前做，点击确定到这里，就必定数组非空
      let ids = this.multipleSelection.map(v => v.logId)
      this.request.post("/LogInfo/del/batch", ids).then(res => {
        // if (res.code == "200") {
        //   this.$message.success("删除成功")
        //   this.load()
        // }else {
        //   this.$message.error("删除失败")
        //   this.load()
        // }
        this.delVisible = false;
        this.$message.success("删除成功")
        this.load();
      })
    },

  },
  mounted() {
    this.logTypes = this.loadAll();
  }

}
</script>

<style scoped>
.headerBg {
  background-color: #42b983 !important;
  color: #333333;
}
</style>
